import "antd/dist/antd.min.css";
import { Avatar, Button, Comment, Form, Input, List } from 'antd';
import moment from 'moment';
import React, { useState, useRef } from 'react';
const { TextArea } = Input;

// 
const CommentList = ({ comments, replyClick }) => (
    <List
        dataSource={comments}
        // header={`${comments.length} ${''}`}
        itemLayout="horizontal"
        // renderItem={(props) => <NestedComment {...props} />}
        renderItem={item => (
            <List.Item>
                <NestedComment comment={item} replyClick={replyClick}></NestedComment>
            </List.Item>
        )}
    />
);

// 
const Editor = ({ onChange, onSubmit, submitting, value, sharedProps }) => (
    <>
        <Form.Item>
            <TextArea rows={4} onChange={onChange} value={value} {...sharedProps} />
        </Form.Item>
        <Form.Item>
            <Button htmlType="submit" loading={submitting} onClick={onSubmit} type="primary">
                
            </Button>
        </Form.Item>
    </>
);

const NestedComment = ({ comment, replyClick }) => {
    return (
        <Comment
            actions={[<span key="comment-nested-reply-to" onClick={replyClick}>Reply to</span>]}
            author={<a>{comment.userName}</a>}
            avatar={<Avatar src={comment.headImg} alt="Han Solo" />}
            content={
                <p>
                    {comment.content}
                </p>
            }
        >{comment.children && comment.children.length > 0 ? (
            <div >
                {/*  Comment  */}
                {comment.children.map((childComment) => (
                    <NestedComment key={childComment.id} comment={childComment} replyClick={replyClick} />
                ))}
            </div>
        ) : null}</Comment>
    );
};

const mockData = {
    "commentList": [
        {
            "userName": "",
            "headImg": "https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png",
            "content": "",
            "children": [
                {
                    "userName": "",
                    "headImg": "https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png",
                    "content": "",
                    "children": [
                        {
                            "userName": "",
                            "headImg": "https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png",
                            "content": "",
                        }
                    ]
                },
                {
                    "userName": "",
                    "headImg": "https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png",
                    "content": "",
                }
            ]
        }
    ]
};


const CommentsPage = () => {
    // 
    const [comments, setComments] = useState([]);
    // 
    const [submitting, setSubmitting] = useState(false);
    const [currentComment, setCommentValue] = useState('');

    // 
    const inputRef = useRef(null);
    const sharedProps = {
        ref: inputRef
    };


    // 
    const handleSubmit = () => {
        if (!currentComment) return;

        setSubmitting(true);
        setTimeout(() => {
            setSubmitting(false);
            setCommentValue('');
            setComments([
                ...comments,
                {
                    author: 'Han Solo',
                    avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
                    content: <p>{currentComment}</p>,
                    datetime: moment('2016-11-22').fromNow(),
                    actions: [<span key="comment-list-reply-to-0" onClick={onReplyClick}>Reply to</span>],
                },
            ]);
        }, 1000);
    };

    const handleChange = (e) => {
        setCommentValue(e.target.value);
    };

    // reply
    const onReplyClick = () => {
        console.log('-------click')
        inputRef.current.focus({
            cursor: 'start',
        });
    }

    return (
        <>
            {mockData.commentList.length > 0 && <CommentList comments={mockData.commentList} replyClick={onReplyClick} />}
            <Comment
                avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" alt="Han Solo" />}
                content={
                    <Editor
                        onChange={handleChange}
                        onSubmit={handleSubmit}
                        submitting={submitting}
                        value={currentComment}
                        sharedProps={sharedProps}
                    />
                }
            />

        </>
    );
};

export default CommentsPage;